<template>
  <div class="hello">
    <h5 class="10">{{time}}</h5>
    <span :class="styl">{{text}}</span>
  </div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
     
    }
  },
  props:{
    time:{
      default:'00:00',
      type:String
    },
    text:{
      default:'数据加载出错',
      type:String
    },
    styl:{
      default:'green',
      type:String
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello{
  margin:20px;
}
h5{
    color:#8df7f3;
    font-size: 0.85rem;
    padding: 5px;
    margin: 5px;
    white-space: nowrap;
    text-align:center;
  }  
span{
      display: inline-block;
      font-size: 0.7rem;
      font-weight: bold;
      margin-top: -8px;
      margin-right: -15px;
      color:#333;
      padding: .25em 1em;
      background: red;
      border-radius: 1em;
      white-space: nowrap;
  }
  .work{
    color:#fff;
    background: #ffd400;
  }
  .play{
    color:#fff;
    background: #00ae9d;
  }
</style>



