<template>
  <div id="app">
    <div id="stars" ref="mystars" >
      <div class="star"></div>
    </div>
    <div class="topbox"><span id="time">{{time}}</span></div>
    <!-- <div class="line"></div> -->
    <div class="bodybox" ref="flow">
      <div class="leftbox">
        <hello v-for="item in evenAM" :time="item.time" :text="item.text" :styl="item.styl"></hello>
      </div>
      <div class="rightbox">
        <hello v-for="item in evenPM" :time="item.time" :text="item.text" :styl="item.styl"></hello>
      </div>
    </div>
  </div>
</template>

<script>
import Hello from './components/Hello'
import BScroll from 'better-scroll'; 
export default {
  name: 'app',
  data(){
    return {
      evenAM:[
        {'time':'10:00 - 10:30','text':'计划任务','styl':'work'},
        {'time':'10:30 - 11:30','text':'学习时间','styl':'work'},
        {'time':'11:30 - 11:45','text':'吃饭','styl':'play'},
        {'time':'11:45 - 13:30','text':'学习时间','styl':'work'},
        {'time':'15:00 - 15:15','text':'休息眼睛喝水','styl':'play'},
        {'time':'15:15 - 15:30','text':'学习时间','styl':'work'},
        {'time':'15:30 - 16:30','text':'活动一个钟','styl':'play'},
        {'time':'16:30 - 18:00','text':'学习时间','styl':'work'}
      ],
      evenPM:[
        {'time':'18:00 - 18:10','text':'休息眼睛喝水','styl':'play'},
        {'time':'18:10 - 18:40','text':'学习时间','styl':'work'},
        {'time':'18:40 - 19:00','text':'吃饭','styl':'play'},
        {'time':'19:00 - 19:30','text':'保护视力餐后散步','styl':'play'},
        {'time':'19:30 - 23:00','text':'逛博客论坛时间','styl':'play'},
        {'time':'23:00 - 24:00','text':'洗澡','styl':'play'}
      ],
      time:0
    }
  },
  components: {
    Hello
  },
  mounted(){
    this.getTime();
    this.funsetInterval();
    this.newstars();
    // this.$nextTick(() => {
    //   this.scroll = new BScroll(this.$refs.flow, {click:true})
    // });
  },
  created(){

  },
  methods:{
    getTime:function(){
      var myhours,myminutes,mytime;
      let mydate = new Date();
      myhours = mydate.getHours();
      myminutes = mydate.getMinutes();
      if(myminutes<10){
        myminutes = '0'+myminutes;
      }
      mytime = Number(myhours+'.'+myminutes);
      this.time=`${myhours}:${myminutes}`;
      console.log(1)
    },
    funsetInterval:function(){
      setInterval(
        this.getTime
      ,50000);
    },
    newstars:function(){
        // 流星
      var mystars = this.$refs.mystars;
      // js随机生成流星
      for (var j=0;j<10;j++) {
        let myheight = window.innerHeight;
        let mywidth = window.innerWidth;
        var newStar = document.createElement("div")
        newStar.className = "star"
        newStar.style.top = randomDistance(myheight-200, -100) + 'px'
        newStar.style.left = randomDistance(100, mywidth) + 'px'
        this.$refs.mystars.appendChild(newStar)
      }
      // 封装随机数方法
      function randomDistance (max, min) {
        var distance = Math.floor(Math.random() * (max - min + 1) + min)
        return distance
      }
       var star = document.getElementsByClassName('star')
        // 给流星添加动画延时
        for (var i = 0, len = star.length; i < len; i++)
        {
      　　  star[i].style.animationDelay = i % 6 == 0 ? '0s' : i * 0.8 + 's'
        }
      }
  }
}

</script>

<style>
body,html{
  font-size:16px;
}
@media screen and (min-width:320px){
  body,html{
    font-size: 16px;
  }
}
@media screen and (min-width:375px){
  body,html{
    font-size: 18px;
  }
}
@media screen and (min-width:414px){
  body,html{
    font-size: 22px;
  }
}
@media screen and (min-width:768px){
  body,html{
    font-size: 28px;
  }
}

#app {
  width:98%;
  height:90%;
  position: absolute;
  top:1vh;
  left:0.2%;
  padding-top: 7vh;
  margin-bottom: -7vh;
  color: #2c3e50;
  text-align: center;
  overflow: hidden;
  background: linear-gradient(top right,#33a3dc, #411445);
  background: -webkit-linear-gradient(top right, #33a3dc, #411445);
  background: -o-linear-gradient(top right, #33a3dc, #411445);
  box-shadow: 2px 6px 5px #411445;
  border-radius:1%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.topbox{
    z-index: 3;
    position: relative;
    width: 20vw;
    height: 20vw;
    left:50%;
    margin-left:-10vw;
    color:#ffe600;
    background: #412f1f;
    border-radius: 50%;
    font-size:1.375rem;
    text-align:center;
    vertical-align: top;
    line-height: 20vw;
}
  #time{
    position:relative;
    display: inline-block;
    width: 100%;
    height: 100%;
  }
  @media screen and (min-width:950px){
  body,html{
    font-size: 36px;
  }
  .topbox{
    z-index: 3;
    position: fixed;
    width: 10vw;
    height: 10vw;
    left:50%;
    top:50%;
    margin-left:-5vw;
    margin-top:-5vw;
    color:#ffe600;
    background: #412f1f;
    border-radius: 50%;
    font-size:1.375rem;
    text-align:center;
    vertical-align: top;
    line-height: 20vw;
  }
    #time{
      position:relative;
      top:-5vw;
      width:100%;
      height:100%;
    }
}
.bodybox{
  width:100vw;
  height: 80vh;
  display: flex;
  justify-content: center;
  overflow-y: auto;
  overflow-x:hidden; 
}
.bodybox::-webkit-scrollbar{
  display: none;
}
.leftbox{
  flex:0 0 50vw;
  width:50vw;
  height:100%;
  padding-bottom:2vh;
  margin-left:-2px;
  border-right:2px dotted rgba(8,255,246,0.12);
}
.rightbox{
  flex:0 0 50vw;
  width:50vw;
  height:100%;
  padding-bottom:2vh;
}

#stars {
        margin: 0 auto;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 2;
      }

      .star {
        display: block;
  　　　width: 1px;
        background: transparent;
        position: relative;
        opacity: 0;
        /*过渡动画*/ 
        animation: star-fall 2s linear infinite;
        -webkit-animation: star-fall 2s linear infinite;
        -moz-animation: star-fall 2s linear infinite;
      }
      
      .star:after {
        content: '';
        display: block;
        border: 0px solid #fff;
        border-width: 0px 90px 2px 150px;
        border-color: transparent transparent transparent rgba(255, 255, 255, .5);
        box-shadow: 0 0 1px 0 rgba(255, 255, 255, .1);
        /*变形*/       
        transform: rotate(-45deg) translate3d(1px, 3px, 0);
        -webkit-transform: rotate(-45deg) translate3d(1px, 3px, 0);
        -moz-transform: rotate(-45deg) translate3d(1px, 3px, 0);
        transform-origin: 0% 100%;
        -webkit-transform-origin: 0% 100%;
        -moz-transform-origin: 0% 100%;
      }
      @keyframes star-fall {
        0% {
          opacity: 0;
          transform: scale(1) translate3d(0, 0, 0);
          -webkit-transform: scale(1) translate3d(0, 0, 0);
          -moz-transform: scale(1) translate3d(0, 0, 0);
        }
        50% {
          opacity: 1;
          transform: translate3d(-200px, 200px, 0);
          -webkit-transform:- translate3d(-200px, 200px, 0);
          -moz-transform: translate3d(-200px, 200px, 0);
        }
        100% {
          opacity: 0;
          transform: scale(1) translate3d(-300px, 300px, 0);
          -webkit-transform: scale(1) translate3d(-300px, 300px, 0);
          -moz-transform: scale(1) translate3d(-300px, 300px, 0);
        }
      }
/*.line{
  position: absolute;
  top:0px;
  left:50%;
  height:100%;
  border-left:1px dotted #FFF;
  background: #009ad6;
  border-radius:100%;
}*/
</style>



